.tooltip:before {
  content: attr(data-text); /* here's the magic */
  position:absolute;
  
  /* basic styles */
  width:200px;
  padding:10px;
  border-radius:10px;
  background:#fff;
  color: black;
  text-align:center;
  font-family: 'JetBrains Mono', monospace;
  font-size: 0.8em;
  font-weight: bold;
  display:none; /* hide by default */
}
.tooltip:hover:before {
  display:block;
  opacity: 1 !important;
  left: 45px !important;
  border: 2px solid black;
}

